* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: "Barlow", sans-serif;
  font-size: 62.5%;
  font-family: "Barlow", sans-serif;
}

.container {
  max-width: 1440px;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.btn {
  width: 175px;
  height: 58px;
  font-size: 16px;
  line-height: 26px;
  text-transform: uppercase;
  font-weight: bold;
  border: none;
  color: #162542;
  background-color: #e8eff2;
  border-radius: 8px;
}

.heading-primary {
  color: #162542;
  font-size: 56px;
  line-height: 56px;
  text-transform: uppercase;
  font-weight: 900;
}

.heading-secondary {
  color: #162542;
  font-style: normal;
  font-weight: 900;
  font-size: 32px;
  line-height: 36px;
  text-transform: uppercase;
}

.heading-teritiary {
  color: #162542;
  font-weight: 900;
  font-size: 24px;
  line-height: 28px;
  text-transform: uppercase;
  grid-row: 2/3;
  margin-right: 3rem;
}

.navbar {
  width: 1110px;
  height: 58px;
  padding: 56px 0rem;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}
.navbar__logo {
  width: 40px;
  height: 40px;
}

.hero {
  width: 1110px;
  padding: 4rem 0rem 2rem 0rem;
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 3rem;
}
.hero__description {
  font-family: Barlow;
  font-style: normal;
  font-weight: 500;
  font-size: 18px;
  line-height: 26px;
  color: #7b8bad;
  margin-top: 1.5rem;
  margin-bottom: 2.5rem;
  margin-right: 4rem;
}
.hero__btn-1 {
  background-color: #f16718;
  color: #ffffff;
  font-weight: 600;
  margin-right: 1rem;
}
.hero__btn-2 {
  background-color: #ffffff;
  color: #7b8bad;
  font-weight: 600;
}
.hero__img {
  width: 540px;
  height: 480px;
  border-radius: 1.2rem;
}

.detail {
  width: 1110px;
  padding: 0rem 0rem;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  gap: 2rem;
}
.detail__box {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.detail__description {
  font-weight: 500;
  font-size: 16px;
  line-height: 26px;
  color: #7b8bad;
  margin-top: 1.5rem;
}

.about {
  width: 1110px;
  padding: 12rem 0rem 8rem 0rem;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-column-gap: 2rem;
  grid-row-gap: 2rem;
  align-items: flex-start;
}
.about__grid {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 65px 56px 130px;
  grid-row-gap: 2.5rem;
}
.about__test {
  background-color: #f16718;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 11px;
  width: 65px;
  height: 65px;
}
.about__icon {
  grid-row: 1/2;
}
.about__description {
  color: #7b8bad;
  font-weight: 500;
  font-size: 16px;
  line-height: 26px;
  grid-row: 3/4;
}

.footer {
  color: #7b8bad;
  height: 26px;
  font-family: Barlow;
  font-style: normal;
  font-size: 16px;
  line-height: 26px;
  padding: 2rem 0rem 4rem 0rem;
}
.footer span {
  font-weight: bold;
}